.worksBlock {
    background-color: darkslateblue;
    min-height: 100vh;
}
.worksContainer {

    flex-direction: column;
    justify-content: flex-start;
}
.works {
    width: 100%;
    border: 1px solid;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}

@media (max-width: 1501px) {
    .works {
        justify-content: center;
    }

}