.skillsBlock {
    min-heigh: 100vh;
    background-color: lightseagreen;
}
.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
}
.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.title {
    background-color: lightsteelblue;
}

@media (max-width: 620px) {
    .skills {
        justify-content: center;
    }
}