.footerBlock {
    min-heigh: 100vh;
    background-color: lightcoral;
    border: 1px solid;
}
.footerContainer {
    min-height: 200px;
    flex-direction: column;
    justify-content: space-around;
}
.iconsWrapper {
    width: 350px;
    min-height: 50px;
    border: 1px solid;
    display: flex;
    justify-content: space-between;

}
.icon {
    height: 50px;
    width: 50px;
    background-color: slategrey;
}

@media (max-width: 450px){
    .iconsWrapper {
        width: 100%;
    }
}
