.work {
    border: 1px solid;
    max-width: 600px;
}
.workIcon {
    position: relative;
}

.workImg {
    max-width: 100%;

}

.descrWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.workBtn{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
}
